export default {
  echartPie(res, name) {
    var option1 = {
      //   var echarts = require("echarts");
      // let myChart = echarts.init(document.getElementById("mainPh"));
      // myChart.setOption({
      grid: {
        top: "30%"
      },
      title: {
        text: "ph",
        left: "5%",
        // fontSize:16
        textStyle: {
          fontWeight: "normal",
          fontSize: 14
        },
        subtextStyle: {
          color: "#000",
          // fontWeight: "bolder",
          fontSize: "14"
        }
      },
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b} :({d}%)"
      },
      color: ["#2de778", "#ffa902"],
      legend: {
        show: false,
        orient: "vertical",
        right: "0",
        bottom: "0",
        // data: ["达标:" + e + "%", "超标:" + f + "%"],
        // data: ["达标:" + e + "%", "超标:" + f + "%"],
        textStyle: {}

        // show: false
      },
      series: [
        {
          label: {
            normal: {
              fontSize: 14,
              position: "inner",
              formatter: "{d}%"
              // formatter: "{b}:{d}%"
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: "30",
                fontWeight: "bold"
              }
            }
          },
          labelLine: {
            normal: {
              show: true
            }
          },
          name: "ph",
          type: "pie",
          // radius: ["55%", "70%"],
          data: [
            // { value: e, name: "达标:" + e + "%" },
            // { value: f, name: "超标:" + f + "%" }
            { value: [0.5] },
            { value: [0.5] }
          ],

          itemStyle: {
            // normal: {
            //   fontSize: 14,
            //   position: 'inner',
            //   formatter: "{b}:{d}%"
            // },
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    };
    var option = {
      title: {
        text: name,
        left: "left",
        top: 0,
        textStyle: {
          color: "#000",
          fontSize: 10
        }
      },
      tooltip: {
        trigger: "none",
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      // color: ["#CD5C5C", "#00CED1", "#9ACD32", "#FFC0CB"],
      color: ["#ffb15a","#4bdbaf"],//,"#01b1ed"
      stillShowZeroSum: false,
      series: [
        {
          name: "达标率分析",
          type: "pie",
          // radius: "80%",
          radius: ['55%', '90%'],
          center: ["50%", "50%"],
          data: [
            { value: (res * 1000).toFixed(1), name: "达标率" },
            { value: ((1 - res) * 1000).toFixed(1), name: "超标率" }
          ],
          label: {
            normal: {
              fontSize: 10,
              position: "inner",
              formatter: "{d}%"
              // formatter: "{b}:{d}%"
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: "10",
                // fontWeight: "bold"
              }
            }
          },
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(128, 128, 128, 0.5)"
            },
            borderWidth:2, //设置border的宽度有多大
            borderColor:'#fff',
          }
        }
      ]
    };
    var option2 = {
      //   backgroundColor: "#2c343c",
      // color: ["#2de778", "#ffa902"],
      color: ["#880e4f", "#01b1ed"],
      title: {
        text: name,
        left: "left",
        top: 0,
        textStyle: {
          color: "#000",
          fontSize: 10
        }
      },

      tooltip: {
        trigger: "none",
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },

      visualMap: {
        show: false,
        min: 100,
        max: 1000,
        inRange: {
          colorLightness: [0, 1]
        }
      },
      series: [
        {
          name: "达标率分析",
          type: "pie",
          radius: "80%",
          center: ["50%", "50%"],
          data: [
            { value: (res * 100).toFixed(1), name: "达标率" },
            { value: ((1 - res) * 100).toFixed(1), name: "超标率" }
          ],
          /**
           * .sort(function(a, b) {
            return a.value - b.value;
          })
           */
          // roseType: "radius",
          label: {
            normal: {
              fontSize: 10,
              position: "inner",
              formatter: "{d}%"
              // formatter: "{b}:{d}%"
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: "10",
                fontWeight: "bold"
              }
            }
          },
          itemStyle: {
            // normal: {
            //   color: "#c23531",
            //   shadowBlur: 200,
            //   shadowColor: "rgba(0, 0, 0, 0.5)"
            // },
            normal: {
              // color:["#880e4f","#01b1ed"]
              color: function(params) {
                //自定义颜色
                var colorList = [
                  // "#880e4f",
                  // "#01b1ed",
                  "#1cbff7",
                  "#2de778",
                  "#ffa902",
                  "#FFFF00",
                  "#FF8C00",
                  "#FF0000",
                  "#FE8463"
                ];
                return colorList[params.dataIndex];
              }
            }
          }

          // animationType: "scale",
          // animationEasing: "elasticOut"
          //   animationDelay: function(idx) {
          //     return Math.random() * 200;
          //   }
        }
      ]
    };
    return option;
  }
};



