<template>
  <div class="backColor">
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <div slot="left" color="#3051f1">
        <a @click="toBack()">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">数据详情</span>
      </div>
    </mu-appbar>

    <div class="contanerMain">
      <div style="background:white;">
        <div class="headerya">
          <img src="static/imgs/pointTitle.png" alt />
          <span>主要污染因子：</span>
        </div>
        <mu-row style>
          <!-- <mu-col span="10" style="font-weight:600;margin-bottom:10px"></mu-col> -->
          <div class="heightTitle" v-if="show==true">
            <div class="flexContent" v-for="(item,index) in arr" :key="index">
                  <span style="margin-right:10px;">{{item.name}}</span>
            </div>
          </div>
        </mu-row>
      </div>
    </div>
    <div class="contHeader">
      <div class="contPaing">
        <div class="headerya">
          <img src="static/imgs/pointTitle.png" alt />
          <span>涉及企业类型：</span>
        </div>
        <div class="contflex" v-for="(item,index) in itemList" :key="index">
          <div>{{item.name}}</div>
          <div style="color:#ccc">{{(item.content.length)?(item.content):"暂无相关企业"}}</div>
        </div>
      </div>
    </div>
    <div class="assContentStyles">
      <div class="headerya">
        <img src="static/imgs/pointTitle.png" alt />
        <span>达标率分析</span>
      </div>
      <div class="assContentStyles">
        <div class="flexEcharts">
          <div id="pH" class="echarts"></div>
          <div id="cod" class="echarts" v-if="showCod===true"></div>
          <div id="ammoniaNitrogen" class="echarts"></div>
          <div id="nitrogen" class="echarts"></div>
          <div id="nickel" class="echarts"></div>
          <div id="cuprum" class="echarts"></div>
          <div id="phosphorus" class="echarts"></div>
          <div id="chromium" class="echarts" v-if="showGe===true"></div>
          <div id="prussiate" class="echarts"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var echarts = require("echarts");
import echartsPie from "../../views/echartsNavi/index";
// var echarts = require("echarts");
import AppBace from "../../../static/ajax";
import { Toast } from "vant";
// import EchartsCommon from "../../../static/js/echartsCommon";

export default {
  name: "Assistant",
  data() {
    return {
      token: "",
      count: 7,
      msg: "",
      res: [],
      arr: [],
      show: false,
      g: "ph",
      loading: false,
      open: false,
      value1: "",
      startTime: AppBace.filter(AppBace.DateTime(30)),
      lastTime: AppBace.filter(new Date()),
      station: this.$store.state.menu[0],
      i: 0,
      showTime: "",
      openTime: false,
      itemList: [],
      showEchart: true,
      showCod:false,
      showGe:false
    };
  },
  created(){
    var name = this.$route.query.stationName;
    if (name==="沙井一期"||name==="沙井二期"||name==="福永净化厂"||name==="固戍净化厂"||name==="中途泵站") {
      this.showCod = true;
    }
     if (name==="沙江5#"||name==="沙江4#"||name==="懋昌东"||name==="松福桥西"||name==="中途泵站") {
      this.showGe = true;
    }

  },
  async mounted() {
    this.token = sessionStorage.getItem("token");
    this.fullscreen();

    await this.initData();
    // await this.initData2();
    this.loading.close();
    // await this.initEcharts();

    var e = ((1 - this.res.data.data.data1[0].ph) * 100).toFixed(1),
      f = (this.res.data.data.data1[0].ph * 100).toFixed(1),
      g = "pH";

    // this.initPhData();
    // EchartsCommon.initPHEcharts(e, f, g);
  },
  methods: {
    //   ph函数
    initEchartsPh(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("pH"));
      var option = echartsPie.echartPie(res, "pH");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "ph",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 氨氮 ammoniaNitrogen
    initEchartsAmmoniaN(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("ammoniaNitrogen"));
      var option = echartsPie.echartPie(res, "氨氮");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "ammoniaNitrogen",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总镍 nickel
    initEchartsNickel(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("nickel"));
      var option = echartsPie.echartPie(res, "总镍");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "nickel",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总铜 cuprum
    initEchartsCuprum(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("cuprum"));
      var option = echartsPie.echartPie(res, "总铜");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "cuprum",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总磷 phosphorus
    initEchartsPhosphorus(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("phosphorus"));
      var option = echartsPie.echartPie(res, "总磷");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "phosphorus",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总铬 chromium
    initEchartsChromium(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("chromium"));
      var option = echartsPie.echartPie(res, "总铬");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "chromium",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总氮 nitrogen
    initEchartsNitrogen(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("nitrogen"));
      var option = echartsPie.echartPie(res, "总氮");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "nitrogen",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // 总氰化物 prussiate
    initEchartsPrussiate(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("prussiate"));
      var option = echartsPie.echartPie(res, "总氰化物");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "prussiate",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    // cod
    initCod(res) {
      var _this = this;
      let myChart = echarts.init(document.getElementById("cod"));
      var option = echartsPie.echartPie(res, "CODcr");
      myChart.setOption(option);
      myChart.on("click", function(param) {
        console.log(param);
        _this.$router.push({
          path: "ExceedingStandardRate",
          query: {
            name: "cod",
            status: param.name,
            startTime: _this.$route.query.startTime,
            endTime: _this.$route.query.endTime,
            station: _this.$route.query.stationName
          }
        });
      });
    },
    toBack() {
      this.$router.go(-1);
    },
    fullscreen() {
      var _this = this;
      this.loading = this.$loading();
      setTimeout(() => {
        this.loading.close();
      }, 3000);
    },
    EchartsInitData(res) {
      for (let index = 0; index < res.length; index++) {
        var arr = res[index];
        for (const key in arr) {
          if (arr.hasOwnProperty(key)) {
            var data = arr[key];
            // debugger
            switch (key) {
              case "ph":
                this.initEchartsPh(data); 
                break;
              case "cuprum":
                // 总铜 cuprum
                this.initEchartsCuprum(data);
                break;
              case "ammoniaNitrogen":
                this.initEchartsAmmoniaN(data);
                break;
              case "nickel":
                this.initEchartsNickel(data);
                break;
              case "phosphorus":
                // 总磷 phosphorus
                this.initEchartsPhosphorus(data);
                break;
              case "chromium":
                // 总铬 chromium
                this.initEchartsChromium(data);
                break;
              case "nitrogen":
                // 总氮 nitrogen
                this.initEchartsNitrogen(data);
                break;
              case "prussiate":
                // 总氰化物 prussiate
                this.initEchartsPrussiate(data);
                break;
              case "cod":
                this.showEchart = true;
                // debugger
                this.initCod(data);
                break;
            }
          }
        }
      }
    },
    compare(property) {
      return function(a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value2 - value1;
      };
    },
    async initData() {
      var url = "/station/water-data/rate/detail";
      // var url = "/station/water-data/exceedAndStandard";
      var waterstation = this.$route.query.stationName;
      var starttime = this.$route.query.startTime;
      var lasttime = this.$route.query.endTime;
      var res = await AppBace.request(
        url,
        "POST",
        {
          waterstation,
          starttime,
          lasttime
        },
        this.token
      );
      this.arr = [];
      // debugger
      console.log("111111111111111",res);
      if (res.data.data.data3 === undefined) {
        Toast(res.data.data.remind);
        setTimeout(() => {
          this.$router.go(-1);
          this.loading.close();
        }, 2000);
      } else {
        
        this.EchartsInitData(res.data.data.data1);
        this.forData(res.data.data.data3);
        this.loading.close();
        this.res = res;
        var arr = res.data.data.data1;
        // let arr = []
        for (var key in res.data.data.data2) {
          console.log(key, res.data.data.data2[key]);
          this.arr.push({
            name: key,
            val: res.data.data.data2[key]
          });
        }

        this.arr.sort(this.compare("val"));
        this.show = true;
        // if (res.data.data.remind == "该时间段没有数据，请重新筛选") {
        //   this.$toast.warning("该时间段没有数据，请重新筛选");
      }
    },
    forData(res) {
      this.itemList = [];
      var arr = [];
      for (const key in res) {
        if (res.hasOwnProperty(key)) {
          var data = res[key];
          var dataCont = "";
          if (data.length > 0) {
            for (let index = 0; index < data.length; index++) {
              dataCont += data[index] + "  ";
            }
          }

          var name = key;
          arr.push({
            name: name,
            content: dataCont
          });
        }
      }
      return (this.itemList = arr);
    },
    forFunc(e, f) {
      for (var key1 in f) {
        if (f.hasOwnProperty(key1)) {
          var element = f[key1];
          for (var key2 in element) {
            if (element.hasOwnProperty(key2)) {
              var element1 = element[key2];
              if (key2 === e) {
                return element1;
              }
            }
          }
        }
      }
    }
  }
};
</script>
<style scoped>
.title {
  font-weight: 600;
  font-size: 14px;
  border-bottom: 1px solid #ccc;
  height: 40px;
  display: flex;
  align-items: center;
}
.contanerMain {
  margin: 0 5px;
  padding: 10px;
  background: white;
  margin-top: 56px;
  /* margin-bottom: 10px; */
  border-radius: 10px;
  background: white;
  box-shadow: 3px 3px 3px #ccc;
  /* margin-bottom: 50px; */
}
.backColor {
  /* background: rgb(230, 228, 228); */
  padding-top: 10px;
  padding-bottom: 10px;
}
.assContentStyles {
  position: relative;
  background: white;
  padding: 10px 10px;
  margin: 0 5px;
  margin-bottom: 10px;
  border-radius: 10px;
  /* box-shadow: 3px 3px 3px #ccc; */
}

.header {
  position: fixed;
  top: 0;
}

.imgSpanFlex {
  display: flex;
  flex-direction: row;
  margin-left: 20px;
}
.imgSpanFlex img {
  width: 12px;
  height: 12px;
}
.imgSpanFlex span {
  margin-left: 10px;
}
.flexContent {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}

.absolute {
  margin: 0 5px;
  color: red;
}
.absolute img {
  width: 20px;
  height: 20px;
}
.icon img {
  width: 28px;
  height: 28px;
}

.select {
  width: 100%;
  text-align: center;
}
.select select {
  width: 100%;
  border-radius: 10px;
  border: #ccc 1px solid;
  outline: none;
  height: 30px;
  text-align: center;
  display: flex;
  justify-content: center;
}

.input {
  width: 100%;
  border-radius: 10px;
  outline: none;
  padding: 0 5px;
  border: 1px solid #ccc;
  height: 30px;
  background: white;
}
.echarts {
  height: 100px;
  width: 45%;
  /* border-bottom: 10px solid rgb(241, 241, 241); */
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.flexEcharts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  /* border-bottom: 5px solid rgb(240, 240, 240); */
}
.contHeader {
  padding: 10px 5px;
}
.contflex {
  display: flex;
  flex-direction: row;
  height: 40px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgb(240, 239, 239);
}
.contPaing {
  padding: 10px;
  background: white;
  border-radius: 10px;
  box-shadow: 3px 3px 3px #ccc;
}
.headerya {
  font-weight: 600;
  border-bottom: 1px solid #ccc;
  height: 40px;
  display: flex;
  align-items: center;
}
.headerya img {
  width: 20px;
  height: 20px;
}
.heightTitle {
  height: 60px;
  display: flex;
  align-items: center;
}
</style>



